.footer-menu {
  display: grid;
  align-self: start;
  justify-content: center;
  font-family: inherit;
  font-size: 25px;
  line-height: 28 / 25 * 1;
  color: var(--basic-main);

  @media (max-width: 1600px) {
    font-size: 21px;
  }

  @media (max-width: 1280px) {
    font-size: 17px;
  }

  @media (max-width: 1080px) {
    justify-content: start;
    font-size: 25px;
  }

  @media (max-width: 480px) {
    justify-content: center;
  }
}

.footer-menu:not(:nth-child(3)) {
  @media (max-width: 1080px) {
    justify-content: center;
  }

  @media (max-width: 767px) {
    justify-content: start;
  }

  @media (max-width: 480px) {
    justify-content: center;
  }
}

.footer-menu__label {
  line-height: 28 / 25 * 1;
  color: var(--basic-main);
  margin-bottom: 32px;

  @media (max-width: 1600px) {
    margin-bottom: 28px;
  }

  @media (max-width: 1280px) {
    margin-bottom: 25px;
  }

  @media (max-width: 992px) {

  }

  @media (max-width: 767px) {

  }

  @media (max-width: 480px) {
    margin-bottom: 15px;
  }
}

.footer-menu a {
  line-height: 32 / 25 * 1;
  color: var(--basic-brand);
}

.footer-menu__contacts {
  display: block;
  margin-top: 30px;

  @media (max-width: 1080px) {
    margin-top: 15px;
  }
}
