.form-user {
  grid-column: 1 / -1;
}

.form-user--basket .field-item--profile {
  grid-template-columns: repeat(3, 1fr);
  column-gap: 8vw;

  @media (max-width: 1600px) {
    column-gap: 7vw;
  }

  @media (max-width: 1080px) {
    grid-template-columns: repeat(auto-fit, minmax(320px, auto));
    column-gap: 80px;
  }

  @media (max-width: 992px) {
    column-gap: 50px;
  }

  @media (max-width: 767px) {
    grid-template-columns: 1fr;
  }
}

.form-user__field input[type="checkbox"] + label,
.form-user__field input[type="radio"] + label {
  cursor: pointer;
}

.form-user__field input,
.form-user__field textarea {
  font-size: 20px;
  color: var(--back-input);
}

.form-user__note {
  font-family: inherit;
  font-size: 25px;
  line-height: 28 / 25 * 1;
  font-variation-settings: 'wght' 300, 'wdth' 75;
  color: var(--basic-main);
  padding-right: 100px;

  @media (max-width: 1600px) {
    font-size: 21px;
  }

  @media (max-width: 1280px) {
    font-size: 17px;
  }

  @media (max-width: 992px) {
    padding-right: 0;
  }
}

.form-user__note a {
  color: var(--basic-brand);
}

.form-user--basket .form-user__note {
  padding-right: 0;
}

.form-user__btn {
  display: block;
  width: 312px;
  margin-top: 15px;
  margin-left: auto;

  @media (max-width: 1600px) {
    width: 260px;
  }

  @media (max-width: 1280px) {
    width: 209px;
  }

  @media (max-width: 1080px) {
    width: 312px;
  }

  @media (max-width: 767px) {
    margin: 25px auto 0;
  }

  @media (max-width: 480px) {
    width: 260px;
  }
}

.form-user__sum {
  display: grid;
  grid-template-columns: auto 1fr;
  row-gap: 3vw;
  align-items: center;
  margin-top: 70px;
  margin-left: -5.4vw;

  @media (max-width: 1600px) {
    margin-top: 55px;
  }

  @media (max-width: 1280px) {
    margin-top: 45px;
  }

  @media (max-width: 1080px) {
    margin-left: 0;
  }

  @media (max-width: 992px) {
    grid-template-columns: 1fr;
    justify-content: center;
  }

  @media (max-width: 767px) {

  }

  @media (max-width: 480px) {

  }
}

.form-user--basket button {
  width: 245px;
  justify-self: end;
  margin-left: 0;

  @media (max-width: 1600px) {
    width: 203px;
  }

  @media (max-width: 1280px) {
    width: 163px;
  }

  @media (max-width: 1080px) {
    width: 245px;
  }

  @media (max-width: 992px) {
    justify-self: center;
  }

  @media (max-width: 767px) {

  }

  @media (max-width: 480px) {

  }
}

.form-user__sum p {
  font-family: inherit;
  font-size: 3.49vw;
  line-height: 69 / 67 * 1;
  font-weight: 700;
  letter-spacing: -0.8px;
  text-align: center;
  color: var(--basic-button);

  @media (max-width: 1600px) {
    font-size: 55px;
    font-size: clamp(28px, 3.5vw, 55px);
  }

  @media (max-width: 1280px) {
    font-size: 45px;
  }

  @media (max-width: 380px) {
    font-size: 40px;
  }
}

.form-user__sum span {
  font-weight: 300;
  color: var(--basic-brand);

  @media (max-width: 680px) {
    display: block;
    margin-top: 7px;
  }
}
