.delivery {

}

.delivery h2 {
  padding-bottom: 75px;

  @media (max-width: 1600px) {
    padding-bottom: 60px;
  }

  @media (max-width: 1280px) {
    padding-bottom: 50px;
  }

  @media (max-width: 767px) {
    padding-left: 0;
  }
}

.delivery > p {
  padding-left: 32px;
  padding-right: 29.17vw;

  @media (max-width: 1600px) {
    padding-left: 27px;
  }

  @media (max-width: 1280px) {
    padding-left: 21px;
  }

  @media (max-width: 1080px) {
    padding-right: 0;
  }

  @media (max-width: 992px) {
    font-size: 21px;
  }

  @media (max-width: 767px) {
    padding-left: 0;
  }

  @media (max-width: 480px) {
    font-size: 17px;
  }
}

.delivery > p:not(:last-child) {
  margin-bottom: 40px;

  @media (max-width: 1600px) {
    margin-bottom: 30px;
  }

  @media (max-width: 1280px) {
    margin-bottom: 20px;
  }

  @media (max-width: 1080px) {
    margin-bottom: 40px;
  }

  @media (max-width: 992px) {
    margin-bottom: 30px;
  }

  @media (max-width: 767px) {
    margin-bottom: 20px;
  }
}
