.announce {
  padding: 0;
}

.announce__list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, auto));
  align-items: center;

  @media (max-width: 1600px) {

  }

  @media (max-width: 1280px) {

  }

  @media (max-width: 1080px) {
    grid-template-columns: repeat(2, 1fr);
  }

  @media (max-width: 992px) {

  }

  @media (max-width: 680px) {
    grid-template-columns: 1fr;
  }

  @media (max-width: 480px) {

  }
}

.announce__item {
  position: relative;
}

.announce__item__img {
  min-height: 368px;

  @media (max-width: 1600px) {
    min-height: 307px;
  }

  @media (max-width: 1280px) {
    min-height: 246px;
  }

  @media (max-width: 1080px) {
     min-height: 400px;
  }

  @media (max-width: 992px) {

  }

  @media (max-width: 767px) {
     min-height: 440px;
  }

  @media (max-width: 480px) {

  }
}

.announce__item__img img {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  object-fit: cover;
}

.announce__decs {
  display: none;
  position: absolute;
  width: 247px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: var(--basic-main);
  font-family: inherit;
  font-weight: 700;
  font-variant: small-caps;

  @media (max-width: 1600px) {
    width: 206px;
  }

  @media (max-width: 1280px) {
    width: 180px;
  }

  @media (max-width: 1080px) {
    width: 265px;
  }

  @media (max-width: 480px) {

  }

  @media (max-width: 380px) {
    width: 200px;
  }
}

.announce__decs p {
  font-size: 34px;
  line-height: 28 / 34 * 1;

  @media (max-width: 1600px) {
    font-size: 27px;
  }

  @media (max-width: 1280px) {
    font-size: 21px;
  }

  @media (max-width: 1080px) {
    font-size: 34px;
  }

  @media (max-width: 992px) {

  }

  @media (max-width: 767px) {
    font-size: 27px;
  }

  @media (max-width: 480px) {

  }
}

.announce__decs .button {
  width: 100%;
  text-decoration: none;
  margin-top: 40px;

  @media (max-width: 1600px) {
    margin-top: 35px;
  }

  @media (max-width: 1280px) {
    margin-top: 28px;
  }

  @media (max-width: 1080px) {
    margin-top: 40px;
  }

  @media (max-width: 992px) {

  }

  @media (max-width: 680px) {
    margin-top: 35px;
  }

  @media (max-width: 480px) {

  }
}

.announce__item:hover img {
  opacity: 0.3;
}

.announce__item:hover .announce__decs {
  display: block;
}
