.catalog-mashines {
  display: grid;
  grid-template-columns: repeat(5, minmax(180px, 384px));
  column-gap: 2.08vw;
  row-gap: 40px;
  justify-content: center;

  @media (max-width: 1600px) {
    column-gap: 1.88vw;
  }

  @media (max-width: 1280px) {
    column-gap: 1.8vw;
  }

  @media (max-width: 1080px) {
    grid-template-columns: repeat(auto-fit, minmax(120px, max-content));
  }

  @media (max-width: 992px) {

  }

  @media (max-width: 767px) {
    grid-template-columns: 1fr;
  }

  @media (max-width: 480px) {

  }
}

.catalog-mashines--index li {
  @media (max-width: 1080px) {
    grid-column: span 2;
  }

  @media (max-width: 767px) {
    grid-column: 1;
  }
}

.catalog-mashines--index li:nth-child(3) {
  @media (max-width: 992px) {
    grid-column: 1 / 3;
  }

  @media (max-width: 767px) {
    grid-column: 1;
  }
}

.catalog-mashines--index li:nth-child(4) {
  @media (max-width: 1080px) {
    grid-column: 2 / 4;
  }

  @media (max-width: 992px) {
    grid-column: 3 / 5;
  }

  @media (max-width: 767px) {
    grid-column: 1;
  }
}

.catalog-mashines--index li:nth-child(5) {
  @media (max-width: 1080px) {
    grid-column: 4 / 6;
  }

  @media (max-width: 992px) {
    grid-column: 2 / 4;
  }

  @media (max-width: 767px) {
    grid-column: 1;
  }
}

.catalog-mashines__link {
  display: flex;
  flex-direction: column;
  height: 100%;
  text-decoration: none;
}

.catalog-mashines__img {
  flex-grow: 1;
  display: flex;
  align-items: flex-end;
  justify-content: center;
  margin-bottom: 40px;

  @media (max-width: 1600px) {
    margin-bottom: 35px;
  }

  @media (max-width: 1280px) {
    margin-bottom: 30px;
  }

  @media (max-width: 992px) {

  }

  @media (max-width: 767px) {

  }

  @media (max-width: 480px) {

  }
}

.catalog-mashines__text {
  text-align: center;
}

.catalog-mashines__text span {
  display: flex;
  flex-direction: column;
}

.catalog-mashines__type {
  font-size: 20px;
  line-height: 24 / 20 * 1;
  font-variation-settings: 'wght' 700, 'wdth' 75;
  letter-spacing: -0.5px;
  text-transform: uppercase;
  margin-bottom: 12px;

  @media (max-width: 1600px) {
    font-size: 17px;
  }

  @media (max-width: 1280px) {
    font-size: 15px;
  }

  @media (max-width: 1080px) {
    font-size: 20px;
  }

  @media (max-width: 992px) {

  }

  @media (max-width: 767px) {

  }

  @media (max-width: 480px) {

  }
}

.catalog-mashines__name {
  font-size: 34px;
  line-height: 26 / 34 * 1;
  font-variation-settings: 'wght' 700, 'wdth' 75;
  font-variant: small-caps;
  margin-bottom: 10px;

  @media (max-width: 1600px) {
    font-size: 28px;
  }

  @media (max-width: 1280px) {
    font-size: 23px;
  }

  @media (max-width: 1080px) {
    font-size: 34px;
  }

  @media (max-width: 992px) {

  }

  @media (max-width: 767px) {

  }

  @media (max-width: 480px) {

  }
}

.catalog-mashines__brand {
  font-size: 25px;
  color: var(--basic-brand);
  font-weight: 300;

  @media (max-width: 1600px) {
    font-size: 21px;
  }

  @media (max-width: 1280px) {
