.logo {
  display: flex;
  align-items: center;
  text-decoration: none;

  @media (max-width: 480px) {
    flex-direction: column;
  }
}

.logo > div {
  width: 106px;
  height: 106px;
  margin-right: 15px;

  @media (max-width: 1600px) {
    width: 89px;
    height: 89px;
  }

  @media (max-width: 1280px) {
    width: 70px;
    height: 70px;
  }

  @media (max-width: 1080px) {
    width: 106px;
    height: 106px;
  }

  @media (max-width: 992px) {
    width: 89px;
    height: 89px;
  }

  @media (max-width: 767px) {
    width: 85px;
    height: 85px;
  }

  @media (max-width: 480px) {

  }
}

.logo p {
  font-family: "Eras", "Arial", sans-serif;
  font-size: 28px;
  line-height: 28 / 28 * 1;
  font-weight: 800;
  color: var(--basic-main);
  text-transform: uppercase;

  @media (max-width: 1600px) {
    font-size: 25px;
  }

  @media (max-width: 1280px) {
    font-size: 21px;
  }

  @media (max-width: 1080px) {
    font-size: 28px;
  }

  @media (max-width: 992px) {
    font-size: 25px;
  }

  @media (max-width: 767px) {

  }

  @media (max-width: 480px) {
    margin-top: 15px;
  }
}
