.catalog-inner {
  display: grid;
  grid-template-columns: 320px 1fr;
  column-gap: 7vw;

  @media (max-width: 1600px) {
    grid-template-columns: 270px 1fr;
  }

  @media (max-width: 1280px) {
    grid-template-columns: 220px 1fr;
  }

  @media (max-width: 1080px) {
    grid-template-columns: 320px 1fr;
  }

  @media (max-width: 992px) {
    grid-template-columns: 270px 1fr;
  }

  @media (max-width: 767px) {
    grid-template-columns: 1fr;
  }
}

.catalog-inner--grain {
  @media (max-width: 992px) {
    grid-template-columns: 1fr;
  }
}

.catalog-inner__sidebar {
  margin-top: 40px;

  @media (max-width: 1600px) {
    margin-top: 32px;
  }

  @media (max-width: 1280px) {
    margin-top: 26px;
  }

  @media (max-width: 767px) {
    margin-top: 0;
    margin-bottom: 35px;
  }
}

ul.catalog-inner__sidebar {
  margin-bottom: 0;
}

.catalog-inner h2 {
  padding-left: 32px;

  @media (max-width: 1600px) {
    padding-left: 27px;
  }

  @media (max-width: 1280px) {
    padding-left: 21px;
  }
}

.catalog-inner .card-goods {
  margin-top: 42px;

  @media (max-width: 1600px) {
    margin-top: 33px;
  }

  @media (max-width: 1280px) {
    margin-top: 25px;
  }

  @media (max-width: 1080px) {
    margin-top: 80px;
  }
}

.catalog-inner .catalog-mashines {
  grid-template-columns: repeat(4, minmax(180px, 384px));
  column-gap: 2.08vw;

  @media (max-width: 1180px) {
    grid-template-columns: repeat(3, minmax(180px, 384px));
  }

  @media (max-width: 1080px) {
    grid-template-columns: repeat(2, minmax(180px, 384px));
  }

  @media (max-width: 992px) {
    column-gap: 5vw;
  }

  @media (max-width: 680px) {
    grid-template-columns: 1fr;
  }
}

.catalog-inner .sidebar {
  @media (max-width: 767px) {
    max-width: 50%;
  }

  @media (max-width: 680px) {
    max-width: 70%;
  }

  @media (max-width: 580px) {
    max-width: 80%;
  }

  @media (max-width: 480px) {
    max-width: 100%;
  }
}

.catalog-inner--grain .sidebar {
  @media (max-width: 992px) {
    max-width: 50%;
  }
}
