.advantages {
  max-width: 100%;
  background-image: url("../img/advantage-back.jpg");
  background-size: cover;
  color: var(--basic-white);
}

.advantages__wrap  {
  display: grid;
  grid-template-columns: max-content 1fr;

  @media (max-width: 1080px) {
    grid-template-columns: repeat(2, 1fr);
  }

  @media (max-width: 767px) {
    grid-template-columns: 1fr;
    justify-content: center;
    row-gap: 17px;
  }
}

.advantages h2 {
  display: grid;
  color: var(--basic-white);
}

.advantages__list {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(3, minmax(200px, 1fr));
  column-gap: 7vw;
  row-gap: 64px;
  align-items: start;
  justify-content: space-between;
  padding-left: 85px;
  padding-right: 85px;
  margin-top: 56px;

  @media (max-width: 1600px) {
    row-gap: 53px;
    padding-left: 64px;
    padding-right: 64px;
    margin-top: 47px;
  }

  @media (max-width: 1280px) {
    row-gap: 43px;
    margin-top: 37px;
  }

  @media (max-width: 1080px) {
    grid-template-columns: repeat(2, minmax(200px, 1fr));
    column-gap: 65px;
    row-gap: 64px;
    margin-top: 23px;
    padding-left: 30px;
    padding-right: 30px;
  }

  @media (max-width: 767px) {
    grid-template-columns: 1fr;
    row-gap: 37px;
    text-align: center;
  }

  @media (max-width: 680px) {
    padding-left: 0;
    padding-right: 0;
  }

  @media (max-width: 380px) {

  }
}

.advantages__item {
  position: relative;
}

.advantages__item::before {
  position: relative;
  display: block;
  content: "";
  height: 162px;
  top: -15px;
  left: 1.56vw;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: 0 100%;

  @media (max-width: 1600px) {
    height: 135px;
    top: -13px;
  }

  @media (max-width: 1280px) {
    height: 108px;
    top: -10px;
  }

  @media (max-width: 1080px) {
    height: 162px;
    top: -15px;
  }

  @media (max-width: 992px) {
    height: 135px;
  }

  @media (max-width: 767px) {
    margin: 0 auto;
  }

  @media (max-width: 680px) {

  }
}

