.advantages__list p {
  line-height: 28 / 25 * 1;
  font-variation-settings: 'wght' 300, 'wdth' 75;
  color: var(--basic-white);
  letter-spacing: -0.8px;

  @media (max-width: 767px) {
    font-size: 25px;
  }

  @media (max-width: 580px) {
    font-size: 23px;
  }
}

p.advantages__title {
  display: block;
  font-size: 42px;
  line-height: 40 / 42 * 1;
  font-variation-settings: 'wght' 300, 'wdth' 75;
  margin-bottom: 12px;

  @media (max-width: 1600px) {
    font-size: 34px;
  }

  @media (max-width: 1280px) {
    font-size: 27px;
  }

  @media (max-width: 1080px) {
    font-size: 42px;
  }

  @media (max-width: 380px) {
    font-size: 34px;
  }
}

.advantages .button {
  max-width: 313px;
  text-decoration: none;
  columns: 2;
  margin-top: 6px;
  margin-left: 75px;

  @media (max-width: 1600px) {
    max-width: 260px;
    margin-left: 63px;
  }

  @media (max-width: 1280px) {
    max-width: 210px;
    margin-left: 50px;
  }

  @media (max-width: 1080px) {
