.navigation {
  background-color: var(--basic-menu);
}

.navigation__wrap {
  padding-top: 15px;
  padding-bottom: 12px;

  @media (max-width: 1600px) {
    padding-top: 13px;
    padding-bottom: 8px;
  }

  @media (max-width: 1280px) {
    padding-top: 9px;
    padding-bottom: 7px;
  }

  @media (max-width: 1080px) {
    padding-top: 15px;
    padding-bottom: 20px;
  }

  @media (max-width: 767px) {
    padding-top: 13px;
  }

  @media (max-width: 480px) {
    padding-bottom: 15px;
  }
}
