.card-goods {
  display: grid;
  grid-template-columns: repeat(6, minmax(120px, 1fr));
  column-gap: 3.28vw;
  row-gap: 5vw;

  @media (max-width: 1600px) {
    column-gap: 2.81vw;
  }

  @media (max-width: 1280px) {
    column-gap: 2.34vw;
  }

  @media (max-width: 1080px) {
    grid-template-columns: repeat(3, minmax(120px, 1fr));
    column-gap: 3.28vw;
    row-gap: 9.07vw;
  }

  @media (max-width: 992px) {
    grid-template-columns: repeat(2, minmax(120px, 1fr));
  }

  @media (max-width: 480px) {
    grid-template-columns: 1fr;
    row-gap: 14vw;
  }
}

.card-goods--col4 {
  grid-template-columns: repeat(4, minmax(120px, 1fr));
  column-gap: 3.28vw;

  @media (max-width: 1080px) {
    grid-template-columns: repeat(3, minmax(120px, 1fr));
  }

  @media (max-width: 992px) {
    grid-template-columns: repeat(2, minmax(120px, 1fr));
  }

  @media (max-width: 480px) {
    grid-template-columns: 1fr;
  }
}

.card-goods li {
  display: flex;
  flex-direction: column;
  height: 100%;
  text-align: center;
}

.card-goods a {
  cursor: pointer;
}

.card-goods__img {
  position: relative;
  display: flex;
  flex-grow: 1;
  align-items: flex-end;
  justify-content: center;
  cursor: pointer;
}

.card-goods__img--hit::before {
  bottom: 5.99vw;
  right: 1.67vw;

  @media (max-width: 1600px) {
    bottom: 7.5vw;
    right: 10%;
    transform: translateX(10%);
  }

  @media (max-width: 1280px) {
    bottom: 10vw;
    right: 1.67vw;
  }

  @media (max-width: 1080px) {
    bottom: 12vw;
    right: 30%;
    transform: translateX(30%);
  }

  @media (max-width: 992px) {
    bottom: 14vw;
    right: 35%;
    transform: translateX(35%);
  }

  @media (max-width: 480px) {
    top: -10vw;
  }
}

.card-goods__img--new::before {
  bottom: 5.99vw;
  right: 1.3vw;

  @media (max-width: 1600px) {
    bottom: 7.5vw;
    right: 10%;
    transform: translateX(10%);
  }

  @media (max-width: 1280px) {
    bottom: 10vw;
    right: 1.3vw;
  }

  @media (max-width: 1080px) {
    bottom: 12vw;
    right: 20%;
    transform: translateX(20%);
  }

  @media (max-width: 992px) {
    bottom: 14vw;
    right: 30%;
    transform: translateX(30%);
  }

  @media (max-width: 480px) {
    top: -10vw;

  }
}

.card-goods__img img {
  position: relative;
}

.card-goods__link {
  font-family: inherit;
  font-size: 23px;
  line-height: 28 / 23 * 1;
  font-variation-settings: 'wght' 300, 'wdth' 70;
  letter-spacing: -0.8px;
  color: var(--basic-brand);
  text-decoration: none;
  margin-top: 18px;

  @media (max-width: 1600px) {
    font-size: 21px;
    margin-top: 15px;
  }

  @media (max-width: 1280px) {
    font-size: 17px;
    margin-top: 12px;
  }

  @media (max-width: 1080px) {
    font-size: 25px;
    margin-top: 18px;
  }

  @media (max-width: 992px) {

  }

  @media (max-width: 767px) {

  }

  @media (max-width: 480px) {

  }
}

.card-goods__price {
  margin-top: 15px;

  @media (max-width: 1600px) {
    margin-top: 10px;
  }

  @media (max-width: 1280px) {
    margin-top: 7px;
  }

  @media (max-width: 1080px) {
    margin-top: 15px;
  }

  @media (max-width: 767px) {

  }
}

.card-goods--col3 {
  grid-template-columns: repeat(auto-fit, minmax(120px, 250px));
  column-gap: 4.17vw;
  row-gap: 80px;
  justify-content: center;
}

.card-goods--col3 li {
  position: relative;
}

.card-goods__count {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 15px;

  @media (max-width: 1600px) {
    margin-top: 10px;
  }

  @media (max-width: 1280px) {
    margin-top: 7px;
  }

  @media (max-width: 1080px) {
    margin-top: 15px;
  }

  @media (max-width: 767px) {

  }
}

.card-goods__count p {
  display: flex;
  justify-content: center;
  padding-left: 15px;
  padding-right: 15px;
}

.card-goods__count input[type="number"] {
  display: inline-block;
  width: 67px;
  height: 33px;
  font-size: 26px;
  font-variation-settings: "wght" 700, "wdth" 75;
  letter-spacing: -0.8px;
  color: var(--basic-brand);
  text-align: center;
  border: 1px solid #000000;
  padding-left: 6px;

  @media (max-width: 1600px) {
    width: 56px;
    height: 28px;
    font-size: 24px;
  }

  @media (max-width: 1280px) {
    width: 45px;
    height: 23px;
    font-size: 21px;
  }

  @media (max-width: 1080px) {
    width: 67px;
    height: 33px;
    font-size: 26px;
  }

  @media (max-width: 767px) {
    width: 45px;
    height: 23px;
    font-size: 21px;
  }
}
