.price-card {
  position: relative;
  display: block;
  max-width: 100%;
  min-height: 50px;
  font-family: inherit;
  font-size: 25px;
  line-height: 50px;
  color: var(--basic-white);
  background: url("../img/price-shop.svg") transparent;
  background-repeat: no-repeat;
  background-size: 143px 50px;
  background-position: 50% 50%;
  cursor: pointer;

  @media (max-width: 1600px) {
    font-size: 21px;
    background-size: 119px 43px;
  }

  @media (max-width: 1280px) {
    font-size: 17px;
    background-size: 95px 34px;
  }

  @media (max-width: 1080px) {
    font-size: 25px;
    background-size: 143px 50px;
  }

  @media (max-width: 992px) {
    background-size: 119px 43px;
  }

  @media (max-width: 767px) {
    background-size: 95px 34px;
  }

  @media (max-width: 480px) {

  }
}
