.label-cofemashines {
  position: relative;
}

.label-cofemashines::before {
  position: absolute;
  width: 145px;
  height: 50px;
  top: 50%;
  transform: translateY(-50%);
  right: 0vw;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: inherit;
  font-size: 23px;
  line-height: 18 / 23 * 1;
  letter-spacing: -1px;
  font-variant: small-caps;
  text-align: center;
  background-repeat: no-repeat;
  background-size: 100% auto;
  z-index: 10;

  @media (max-width: 1600px) {
    width: 119px;
    height: 43px;
    font-size: 20px;
  }

  @media (max-width: 1280px) {
    width: 95px;
    height: 34px;
    font-size: 16px;
  }

  @media (max-width: 1080px) {
    width: 145px;
    height: 50px;
    font-size: 23px;
    top: 50%;
    transform: translateY(-50%);
  }

  @media (max-width: 680px) {
    width: 119px;
    height: 43px;
    font-size: 20px;
    right: 20%;
  }

  @media (max-width: 480px) {
    width: 95px;
    height: 34px;
    font-size: 16px;
  }
}

