.business {
  max-width: 100%;
  background-color: var(--basic-brand);
  padding: 20px 40px;

  @media (max-width: 380px) {
    padding: 20px 80px;
  }
}

.business p {
  font-variation-settings: 'wght' 300, 'wdth' 75;
  color: var(--basic-white);
  text-align: right;
}

.business p {
  font-size: 16px;
  line-height: 20 / 16 * 1;
  font-variation-settings: 'wght' 300, 'wdth' 75;
  color: var(--basic-white);
  text-align: right;

  @media (max-width: 1600px) {
    font-size: 14px;
  }

  @media (max-width: 1280px) {
    font-size: 12px;
  }

  @media (max-width: 1080px) {
    font-size: 16px;
  }

  @media (max-width: 767px) {
    text-align: center;
  }
}

.business a {
  color: var(--basic-white);
}

