.button {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 58px;
  font-family: inherit;
  font-size: 25px;
  line-height: 23 / 25 * 1;
  font-weight: 300;
  text-align: center;
  letter-spacing: -0.3px;
  color: var(--basic-text-button);
  background-color: var(--basic-button);
  font-variant: small-caps;
  padding: 8px 20px;
  cursor: pointer;

  @media (max-width: 1600px) {
    min-height: 48px;
    font-size: 21px;
  }

  @media (max-width: 1280px) {
    min-height: 38px;
    font-size: 17px;
  }

  @media (max-width: 1080px) {
    min-height: 58px;
    font-size: 25px;
  }

  @media (max-width: 992px) {

  }

  @media (max-width: 680px) {
    min-height: 48px;
    font-size: 21px;
  }

  @media (max-width: 480px) {

  }
}
