.item-product {

}

.item-product__wrap {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 3.28vw;
  margin-top: 34px;

  @media (max-width: 1080px) {
    grid-template-columns: 1fr;
  }

  @media (max-width: 380px) {

  }
}

.item-product__img {
  flex-grow: 1;
  display: flex;
  align-items: flex-end;
  justify-content: center;

  @media (max-width: 1600px) {

  }

  @media (max-width: 1280px) {

  }

  @media (max-width: 992px) {

  }

  @media (max-width: 767px) {

  }

  @media (max-width: 480px) {

  }
}

.item-product__info {
  padding-right: 9.38vw;

  @media (max-width: 1080px) {
    margin-top: 50px;
  }

  @media (max-width: 767px) {
    padding-right: 0;
  }
}

.item-product__info h4 {
  position: relative;
  width: 239px;
  height: 58px;

  @media (max-width: 1600px) {
    width: 197px;
    height: 46px;
  }

  @media (max-width: 1280px) {
    width: 157px;
    height: 36px;
  }

  @media (max-width: 992px) {

  }

  @media (max-width: 767px) {

  }

  @media (max-width: 480px) {

  }
}

.item-product__info h4 img {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  object-fit: cover;
}

.item-product__parameters {
  margin-top: 35px;
  margin-bottom: 50px;

  @media (max-width: 1600px) {
    margin-top: 30px;
    margin-bottom: 42px;
  }

  @media (max-width: 1280px) {
    margin-top: 26px;
    margin-bottom: 33px;
  }

  @media (max-width: 992px) {

  }

  @media (max-width: 767px) {

  }

  @media (max-width: 480px) {

  }
}

.item-product__info p {
  font-family: inherit;
  font-size: 29px;
  line-height: 34 / 29 * 1;
  font-variation-settings: 'wght' 100, 'wdth' 75;
  letter-spacing: -0.8px;

  @media (max-width: 1600px) {
    font-size: 24px;
  }

  @media (max-width: 1280px) {
    font-size: 19px;
  }

  @media (max-width: 1080px) {
    font-size: 29px;
  }

  @media (max-width: 992px) {
    font-size: 24px;
  }

  @media (max-width: 767px) {
    font-size: 19px;
  }

  @media (max-width: 480px) {

  }
}

.item-product__info p:not(:last-child) {
  margin-bottom: 40px;

  @media (max-width: 1600px) {
    margin-bottom: 30px;
  }

  @media (max-width: 1280px) {
    margin-bottom: 20px;
  }

  @media (max-width: 1080px) {
    margin-bottom: 40px;
  }

  @media (max-width: 992px) {
    margin-bottom: 30px;
  }

  @media (max-width: 767px) {
    margin-bottom: 20px;
  }
}

.item-product__parameters > p {
  font-family: inherit;
  font-size: 34px;
  line-height: 42 / 34 * 1;
  font-variation-settings: 'wght' 700, 'wdth' 75;

  @media (max-width: 1600px) {
    font-size: 28px;
  }

  @media (max-width: 1280px) {
    font-size: 23px;
  }

  @media (max-width: 1080px) {
    font-size: 34px;
  }

  @media (max-width: 992px) {
    font-size: 28px;
  }

  @media (max-width: 767px) {
    font-size: 23px;
  }

  @media (max-width: 480px) {

  }
}

.item-product__parameters span {
  position: relative;
  display: inline-block;
  margin-right: 120px;
  margin-top: 10px;

  @media (max-width: 1600px) {
    margin-right: 100px;
  }

  @media (max-width: 1280px) {
    margin-right: 80px;
  }

  @media (max-width: 1080px) {
    margin-right: 120px;
  }

  @media (max-width: 992px) {
    margin-right: 100px;
  }

  @media (max-width: 767px) {
    margin-right: 80px;
  }
}

.item-product__parameters span::after {
  position: absolute;
  content: "";
  width: 70px;
  height: 77px;
  top: 50%;
  transform: translateY(-50%);
  left: 63px;
  background-image: url("../img/work1.svg");
  background-repeat: no-repeat;
  background-size: 100% auto;

  @media (max-width: 1600px) {
    width: 60px;
    height: 67px;
    left: 53px;
  }

  @media (max-width: 1280px) {
    width: 47px;
    height: 50px;
    left: 43px;
  }

  @media (max-width: 1080px) {
    width: 70px;
    height: 77px;
    left: 63px;
  }

  @media (max-width: 992px) {
    width: 60px;
    height: 67px;
    left: 53px;
  }

  @media (max-width: 767px) {
    width: 47px;
    height: 50px;
    left: 43px;
  }

  @media (max-width: 480px) {

  }
}

.item-product__buy {
  display: grid;
  grid-template-columns: repeat(2, max-content);
  align-items: center;
  justify-items: start;
  font-family: inherit;
  margin-top: 57px;

  @media (max-width: 380px) {
    grid-template-columns: max-content;
    justify-items: center;
    justify-content: center;
  }
}

.item-product__buy .price-card {
  width: 143px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--basic-white);
  text-align: center;
  background-position: 0 50%;

  @media (max-width: 1600px) {
    width: 119px;
  }

  @media (max-width: 1280px) {
    width: 95px;
  }

  @media (max-width: 1080px) {
    width: 143px;
    font-size: 25px;
  }
}

.item-product__btn {
  font-family: inherit;
  font-size: 34px;
  font-variation-settings: 'wght' 700, 'wdth' 75;
  letter-spacing: -1px;
  color: var(--basic-brand);
  background: transparent;
  margin-left: 80px;
  cursor: pointer;

  @media (max-width: 1600px) {
    font-size: 28px;
    margin-left: 65px;
  }

  @media (max-width: 1280px) {
    font-size: 23px;
    margin-left: 52px;
  }

  @media (max-width: 1080px) {
    font-size: 34px;
    margin-left: 80px;
  }

  @media (max-width: 767px) {
    font-size: 28px;
    margin-left: 65px;
  }

  @media (max-width: 380px) {
    margin-top: 15px;
    margin-left: 0;
  }
}
