.application {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  align-items: center;
  max-width: 100%;
  min-height: 800px;
  background-color: #fff6ea;
  padding-top: 70px;
  padding-left: 9.9vw;
  padding-right: 9.9vw;
  padding-bottom: 82px;

  @media (max-width: 1600px) {
    min-height: 672px;
    padding-top: 60px;
    padding-left: 9.6vw;
    padding-right: 9.6vw;
    padding-bottom: 67px;
}

  @media (max-width: 1280px) {
    min-height: 537px;
    padding-top: 47px;
    padding-bottom: 54px;
  }

  @media (max-width: 1080px) {
    padding-left: 60px;
    padding-right: 60px;
  }

  @media (max-width: 992px) {
    padding-left: 30px;
    padding-right: 30px;
  }

  @media (max-width: 767px) {
    padding-left: 20px;
    padding-right: 20px;
  }
}

.application h3 {
  grid-column: span 2;
  font-family: inherit;
  font-size: 67px;
  line-height: 69 / 67 * 1;
  font-variation-settings: 'wght' 700, 'wdth' 75;
  color: var(--basic-button);

  @media (max-width: 1600px) {
    font-size: 55px;
  }

  @media (max-width: 1280px) {
    font-size: 45px;
  }

  @media (max-width: 1080px) {
    grid-column: 1 / -1;
    font-size: 67px;
  }

  @media (max-width: 992px) {
    font-size: 60px;
  }

  @media (max-width: 767px) {
    font-size: 50px;
  }

  @media (max-width: 480px) {
    font-size: 42px;
  }

  @media (max-width: 380px) {
    font-size: 38px;
  }
}

.application > p {
  display: grid;
  grid-column: span 2;
  font-family: inherit;
  font-size: 25px;
  line-height: 28 / 25 * 1;
  font-variation-settings: 'wght' 300, 'wdth' 75;
  color: var(--basic-brand);
  padding-left: 1.04vw;

  @media (max-width: 1600px) {
    font-size: 21px;
  }

  @media (max-width: 1280px) {
    font-size: 17px;
  }

  @media (max-width: 1080px) {
    grid-column: 1 / -1;
    font-size: 25px;
    padding-left: 20px;
    margin-top: 7px;
  }

  @media (max-width: 992px) {
    font-size: 21px;
  }

  @media (max-width: 480px) {
    font-size: 17px;
  }
}

.application > p::before {
  position: relative;
  content: "";
  width: 30px;
  height: 27px;
  top: 10px;
  left: -1.82vw;
  background-image: url("../img/star.png");
  background-repeat: no-repeat;
  background-size: 100% auto;

  @media (max-width: 1600px) {
    width: 23px;
    height: 21px;
  }

  @media (max-width: 1280px) {
    width: 18px;
    height: 15px;
  }

  @media (max-width: 1080px) {
    left: -20px;
  }
}

.application__form {
  padding-left: 5.4vw;
  padding-right: 5.4vw;
  margin-top: 45px;

  @media (max-width: 1600px) {
    margin-top: 36px;
  }

  @media (max-width: 1280px) {
    margin-top: 30px;
  }

  @media (max-width: 1080px) {
    padding-left: 0;
    padding-right: 0;
  }
}

.application--order {
  grid-template-columns: 1fr;
}

.application--order > p {
  color: var(--basic-main);
  padding-left: 103px;
  margin-top: 45px;

  @media (max-width: 1600px) {
    margin-top: 40px;
  }

  @media (max-width: 1280px) {
    margin-top: 35px;
  }

  @media (max-width: 1080px) {
    margin-top: 45px;
    padding-left: 0;
  }

  @media (max-width: 992px) {
    margin-top: 40px;
  }

  @media (max-width: 767px) {
    margin-top: 30px;
  }

  @media (max-width: 480px) {
    margin-top: 25px;
  }
}

.application--order > p::before {
  display: none;
}

.application--order span {
  color: var(--basic-brand);
}

.application--order form {
  margin-top: 87px;

  @media (max-width: 1600px) {
    margin-top: 72px;
  }

  @media (max-width: 1280px) {
    margin-top: 57px;
  }

  @media (max-width: 1080px) {
    margin-top: 87px;
  }

  @media (max-width: 992px) {

  }

  @media (max-width: 767px) {
    margin-top: 45px;

  }

  @media (max-width: 480px) {
    margin-top: 30px;
  }
}
