.sidebar {
  @media (max-width: 767px) {
    max-width: 50%;
  }

  @media (max-width: 680px) {
    max-width: 70%;
  }

  @media (max-width: 580px) {
    max-width: 80%;
  }

  @media (max-width: 480px) {
    max-width: 100%;
  }
}

.sidebar:not(:first-child) {
  margin-top: 130px;

  @media (max-width: 1600px) {
    margin-top: 100px;
  }

  @media (max-width: 1280px) {
    margin-top: 80px;
  }

  @media (max-width: 992px) {

  }

  @media (max-width: 767px) {

  }

  @media (max-width: 480px) {

  }
}

.sidebar li:not(:last-child) {
  margin-bottom: 12px;
}

.sidebar a {
  font-family: inherit;
  font-size: 25px;
  line-height: 28 / 25 * 1;
  color: var(--basic-main);
  letter-spacing: -0.8px;

  @media (max-width: 1600px) {
    font-size: 21px;
  }

  @media (max-width: 1280px) {
    font-size: 17px;
  }

  @media (max-width: 1080px) {
    font-size: 25px;
  }

  @media (max-width: 992px) {

  }

  @media (max-width: 767px) {

  }

  @media (max-width: 480px) {

  }
}

.sidebar > li > a {
  display: block;
  font-variation-settings: 'wght' 700, 'wdth' 70;
  text-decoration: none;
  border-bottom: 4px solid var(--basic-main);
}

a.sidebar__link-active {
  color: var(--basic-brand);
  text-decoration: none;
}
