.content {

}

.content h2 {
  font-family: inherit;
  font-size: 83px;
  line-height: 87 / 83 * 1;
  font-variation-settings: 'wght' 700, 'wdth' 75;
  color: var(--basic-main);
  padding-left: 7.81vw;

  @media (max-width: 1600px) {
    font-size: 69px;
    padding-left: 7.8vw;
  }

  @media (max-width: 1280px) {
    font-size: 55px;
  }

  @media (max-width: 1080px) {
    font-size: 83px;
  }

  @media (max-width: 992px) {
    font-size: 60px;
    padding-left: 30px;
  }

  @media (max-width: 767px) {
    font-size: 50px;
    text-align: center;
    padding-left: 0;
  }

  @media (max-width: 480px) {
    font-size: 42px;
  }

  @media (max-width: 380px) {
    font-size: 38px;
  }
}

.content h2 > a {
  color: var(--basic-main);
}

.content p {
  font-family: inherit;
  font-size: 25px;
  line-height: 28 / 25 * 1;
  font-variation-settings: 'wght' 300, 'wdth' 75;
  color: var(--basic-main);

  @media (max-width: 1600px) {
    font-size: 21px;
  }

  @media (max-width: 1280px) {
    font-size: 17px;
  }

  @media (max-width: 1080px) {
    font-size: 25px;
  }

  @media (max-width: 767px) {
    font-size: 21px;
  }

  @media (max-width: 480px) {
    font-size: 17px;
  }
}
