.slider-main {

}

.slider-main__item {
  position: relative;
  width: 100%;
  min-height: 568px;

  @media (max-width: 1600px) {
    min-height: 483px;
  }

  @media (max-width: 1280px) {
    min-height: 386px;
  }

  @media (max-width: 992px) {

  }

  @media (max-width: 767px) {

  }

  @media (max-width: 480px) {

  }
}

.slider-main__item > img {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  object-fit: cover;
}

.slider-main__desc {
  position: relative;
}

.slider-main__desc h1 {
  margin-bottom: 35px;
}

.slider-main__button {
  max-width: 190px;
  margin-top: 55px;

  @media (max-width: 1600px) {
    max-width: 158px;
    margin-top: 50px;
  }

  @media (max-width: 1280px) {
    max-width: 126px;
    margin-top: 40px;
  }

  @media (max-width: 1080px) {
    max-width: 190px;
    margin-top: 55px;
  }

  @media (max-width: 992px) {

  }

  @media (max-width: 680px) {
    max-width: 158px;
    margin-top: 50px;
  }

  @media (max-width: 480px) {
    max-width: 126px;
    margin-top: 40px;
    margin: 40px auto 0;
  }
}

.slider-main__toggle {
  position: absolute;
  width: 63px;
  height: 63px;
  top: 45%;
  border: none;
  cursor: pointer;
  z-index: 15;

  @media (max-width: 1600px) {
    width: 53px;
    height: 53px;
  }

  @media (max-width: 1280px) {
    width: 43px;
    height: 43px;
  }

  @media (max-width: 1080px) {
    width: 63px;
    height: 63px;
  }

  @media (max-width: 992px) {

  }

  @media (max-width: 767px) {
    width: 37px;
    height: 37px;
  }

  @media (max-width: 580px) {
    width: 35px;
    height: 35px;
  }

  @media (max-width: 380px) {
    display: none;
  }
}

.slider-main__toggle--prev {
  left: 40px;
  transform: scaleX(-1);

  @media (max-width: 1080px) {
    left: 0;
  }
}

.slider-main__toggle--next {
  right: 40px;

  @media (max-width: 1080px) {
    right: 0;
  }
}

.slider-main__toggle::before {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  content: "\276F";
  font-size: 45px;
  width: 63px;
  height: 63px;
  color: var(--basic-main);
  background-color: #e1ac62;
  padding-left: 10px;
  padding-right: 10px;

  @media (max-width: 1600px) {
    font-size: 38px;
    width: 53px;
    height: 53px;
  }

  @media (max-width: 1280px) {
    font-size: 30px;
    width: 43px;
    height: 43px;
  }

  @media (max-width: 1080px) {
    font-size: 45px;
    width: 63px;
    height: 63px;
  }

  @media (max-width: 992px) {

  }

  @media (max-width: 767px) {
    font-size: 25px;
    width: 37px;
    height: 37px;
  }

  @media (max-width: 580px) {
    font-size: 23px;
    width: 35px;
    height: 36px;
  }

  @media (max-width: 380px) {
    display: none;
  }
}

.slider-main__toggle.swiper-button-disabled {
  opacity: 0.5;
}
