.menu {
  width: 54.17vw;
  display: grid;
  grid-template-columns: repeat(6, max-content);
  column-gap: 1.4vw;
  row-gap: 0.5vw;
  align-items: center;

  @media (max-width: 1600px) {
    width: 54.38vw;
  }

  @media (max-width: 1280px) {
    width: 50vw;
  }

  @media (max-width: 1180px) {
    grid-template-columns: repeat(3, max-content);
    column-gap: 4vw;
  }

  @media (max-width: 1080px) {
    width: 100%;
    row-gap: 1vw;
    justify-content: start;
  }

  @media (max-width: 992px) {

  }

  @media (max-width: 767px) {
    grid-template-columns: repeat(2, max-content);
    column-gap: 8vw;
    row-gap: 1.5vw;
  }

  @media (max-width: 680px) {
    grid-template-columns: 1fr;
    row-gap: 2vw;
    justify-content: center;
  }

  @media (max-width: 480px) {
    row-gap: 3vw;
  }
}

.menu li {
  font-family: inherit;
  font-size: 25px;
  line-height: 28 / 25 * 1;
  font-weight: 300;
  letter-spacing: -0.5px;
  font-variant: small-caps;

  @media (max-width: 1600px) {
    font-size: 21px;
  }

  @media (max-width: 1280px) {
    font-size: 17px;
  }

  @media (max-width: 1180px) {
    font-size: 21px;
  }

  @media (max-width: 1080px) {
    font-size: 25px;
  }

  @media (max-width: 992px) {

  }

  @media (max-width: 767px) {

  }

  @media (max-width: 480px) {

  }

  &:hover {
    font-size: 25px;
    text-transform: uppercase;

    @media (max-width: 1600px) {
      font-size: 21px;
    }

    @media (max-width: 1280px) {
      font-size: 17px;
    }

    @media (max-width: 1180px) {
      font-size: 21px;
    }

    @media (max-width: 1080px) {
      font-size: 25px;
    }

    @media (max-width: 992px) {

    }

    @media (max-width: 767px) {

    }

    @media (max-width: 480px) {

    }

    &:active {
      opacity: 0.5;
    }
  }
}

.menu a {
  color: var(--basic-white);
  text-decoration: none;
}

a.menu__link-active {
  font-size: 25px;
  text-transform: uppercase;
  color: var(--basic-main);
  padding-bottom: 6px;
  border-bottom: 4px solid var(--basic-main);

  @media (max-width: 1600px) {
    font-size: 21px;
    padding-bottom: 4px;
    border-bottom: 3px solid var(--basic-main);
  }

  @media (max-width: 1280px) {
    font-size: 17px;
    border-bottom: 2px solid var(--basic-main);
  }

  @media (max-width: 1080px) {
    font-size: 25px;
    border-bottom: 4px solid var(--basic-main);
  }

  @media (max-width: 992px) {

  }

  @media (max-width: 767px) {

  }

  @media (max-width: 480px) {

  }
}
