.main-nav {
  position: relative;
  display: grid;
  grid-template-columns: repeat(3, max-content);
  column-gap: 4.17vw;
  align-items: center;
  padding-left: 6.25vw;
  padding-right: 6.25vw;

  @media (max-width: 1600px) {
    column-gap: 3vw;
    padding-left: 5.63vw;
    padding-right: 5.63vw;
  }

  @media (max-width: 1280px) {
    column-gap: 4.69vw;
  }

  @media (max-width: 1080px) {
    grid-template-columns: 1fr;
    justify-content: space-between;
    padding-left: 6.25vw;
    padding-right: 2.96vw;
  }

  @media (max-width: 992px) {

  }

  @media (max-width: 680px) {
    text-align: center;
  }

  @media (max-width: 580px) {
    padding-left: 36px;
    padding-right: 36px;
  }
}

.main-nav__menu {
  @media (max-width: 1080px) {
    margin-top: 0px;
  }

  @media (max-width: 680px) {
    margin-top: 0;
  }
}

.main-nav__basket {
  position: relative;
  display: flex;
  width: 37px;
  height: 30px;
  text-decoration: none;
  justify-self: end;

  @media (max-width: 1600px) {
    width: 31px;
    height: 25px;
  }

  @media (max-width: 1280px) {
    width: 25px;
    height: 20px;
  }

  @media (max-width: 1080px) {
    position: absolute;
    width: 37px;
    height: 30px;
    top: 50px;
    right: 33px;
  }

  @media (max-width: 992px) {
    top: 45px;
  }

  @media (max-width: 580px) {
    right: 25px;
  }
}

.main-nav__basket span {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 36px;
  height: 36px;
  top: -0.63vw;
  left: 1.56vw;
  font-size: 17px;
  line-height: 1px;
  font-variation-settings: 'wght' 300, 'wdth' 75;
  color: var(--basic-white);
  background-color: var(--basic-main);
  border-radius: 50%;

  @media (max-width: 1600px) {
    width: 30px;
    height: 30px;
    font-size: 14px;
  }

  @media (max-width: 1280px) {
    width: 24px;
    height: 24px;
    font-size: 12px;
  }

  @media (max-width: 1080px) {
    width: 36px;
    height: 36px;
    font-size: 17px;
    top: -12px;
    left: 30px;
  }
}


/*  Сворачивание Меню  */

.main-nav__toggle {
  display: none;

  @media (max-width: 1080px) {
    position: absolute;
    display: block;
    width: 26px;
    height: 15px;
    top: 55px;
    left: 0;
    background-color: transparent;
    padding: 0;
    border: none;
    outline: none;
    cursor: pointer;
    z-index: 30;
  }

  @media (max-width: 992px) {
    top: 50px;
  }

  @media (max-width: 767px) {
    top: 45px;
  }

  @media (max-width: 480px) {
    top: 42px;
  }
}

.main-nav--closed .main-nav__toggle::before {
  @media (max-width: 1080px) {
    position: absolute;
    content: "";
    width: 26px;
    height: 3px;
    top: 0;
    left: 0;
    background-color: var(--basic-white);
    box-shadow: 0 6px 0 0 var(--basic-white), 0 12px 0 0 var(--basic-white);
  }
}

.main-nav--closed .main-nav__toggle:hover::before,
.main-nav--closed .main-nav__toggle:focus::before {
  opacity: 0.5;
}

.main-nav--closed .main-nav__toggle:active::before {
  opacity: 0.2;
}

.main-nav--opened .main-nav__toggle {
  @media (max-width: 1080px) {
    width: 26px;
    height: 26px;
    top: 55px;
    background-color: transparent;
  }

  @media (max-width: 992px) {
    top: 50px;
  }

  @media (max-width: 767px) {
    top: 45px;
  }

  @media (max-width: 480px) {
    top: 42px;
  }
}

.main-nav--opened .main-nav__toggle::before,
.main-nav--opened .main-nav__toggle::after {
  @media (max-width: 1080px) {
    content: "";
    position: absolute;
    width: 26px;
    height: 3px;
    top: 50%;
    transform: translateY(-50%);
    left: 0;
    background-color: var(--basic-white);
  }
}

.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
}

.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}

.main-nav--opened .main-nav__toggle:hover::before,
.main-nav--opened .main-nav__toggle:hover::after,
.main-nav--opened .main-nav__toggle:focus::before,
.main-nav--opened .main-nav__toggle:focus::after {
  opacity: 0.5;
}

.main-nav--opened .main-nav__toggle:active::before,
.main-nav--opened .main-nav__toggle:active::after {
  opacity: 0.2;
}

.main-nav--closed .main-nav__menu li {
  @media (max-width: 1080px) {
    display: none;
  }
}

.main-nav--opened .main-nav__menu {
  @media (max-width: 1080px) {
    padding-top: 0;
    padding-bottom: 0;
    margin-top: 30px;
  }
}
