.nav-footer {
  display: grid;
  grid-template-columns: max-content repeat(3, 1fr);
  justify-content: space-between;
  row-gap: 80px;
  padding-top: 53px;
  padding-left: 95px;
  padding-right: 95px;
  padding-bottom: 57px;
  letter-spacing: -0.5px;

  @media (max-width: 1600px) {
    padding-top: 45px;
    padding-left: 78px;
    padding-right: 78px;
    padding-bottom: 50px;
  }

  @media (max-width: 1280px) {
    padding-top: 35px;
    padding-left: 63px;
    padding-right: 63px;
    padding-bottom: 40px;
  }

  @media (max-width: 1080px) {
    grid-template-columns: repeat(2, 1fr);
    justify-content: center;
    padding-top: 45px;
    padding-left: 12vw;
    padding-bottom: 57px;
  }

  @media (max-width: 767px) {
    grid-template-columns: max-content;
    justify-content: center;
  }

  @media (max-width: 580px) {
    padding-left: 20px;
    padding-right: 20px;
  }

  @media (max-width: 480px) {
    grid-template-columns: 1fr;
    row-gap: 60px;
    text-align: center;
  }
}

.nav-footer__contacts {
  width: 350px;

  @media (max-width: 1600px) {
    width: 280px;
  }

  @media (max-width: 1080px) {
    width: 350px;
  }

  @media (max-width: 480px) {
    width: 100%;
  }
}

.nav-footer__copyright {
  margin-bottom: 32px;

  @media (max-width: 1600px) {
    margin-bottom: 28px;
  }

  @media (max-width: 1280px) {
    margin-bottom: 25px;
  }

  @media (max-width: 992px) {

  }

  @media (max-width: 767px) {

  }

  @media (max-width: 480px) {

  }
}

p.nav-footer__info {
  font-size: 16px;
  line-height: 20 / 16 * 1;
  margin-bottom: 23px;

  @media (max-width: 1600px) {
    font-size: 14px;
    margin-bottom: 18px;
  }

  @media (max-width: 1280px) {
    font-size: 12px;
    margin-bottom: 15px;
  }

  @media (max-width: 1080px) {
    font-size: 16px;
  }

  @media (max-width: 767px) {

  }

  @media (max-width: 480px) {

  }
}

.nav-footer__info a {
  display: inline-block;
  color: var(--basic-main);
  text-decoration: none;
}

.nav-footer__mail {
  font-size: 25px;
  line-height: 28 / 25 * 1;
  color: var(--basic-brand);

  @media (max-width: 1600px) {
    font-size: 21px;
  }

  @media (max-width: 1280px) {
    font-size: 17px;
  }

  @media (max-width: 1080px) {
    font-size: 25px;
  }

  @media (max-width: 767px) {

  }

  @media (max-width: 480px) {

  }
}
