.header-top {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
  padding-top: 13px;
  padding-bottom: 13px;

  @media (max-width: 1600px) {
    padding-top: 10px;
    padding-bottom: 10px;
  }

  @media (max-width: 1280px) {
    padding-top: 8px;
    padding-bottom: 8px;
  }

  @media (max-width: 1080px) {
    justify-content: space-between;
    padding-top: 13px;
    padding-bottom: 13px;
  }

  @media (max-width: 992px) {

  }

  @media (max-width: 767px) {

  }

  @media (max-width: 480px) {
    flex-direction: column;
  }
}

.header-top form {
  margin-right: 74px;

  @media (max-width: 1600px) {
    margin-right: 60px;
  }

  @media (max-width: 1280px) {
    margin-right: 50px;
  }

  @media (max-width: 1080px) {
    margin-right: 0;
  }

  @media (max-width: 767px) {

  }

  @media (max-width: 480px) {

  }
}

.header-top__tel {
  font-family: "Eras", "Arial", sans-serif;
  font-size: 29px;
  line-height: 30 / 29 * 1;
  font-weight: 700;
  color: var(--basic-main);
  text-decoration: none;

  @media (max-width: 1600px) {
    font-size: 24px;
  }

  @media (max-width: 1280px) {
    font-size: 20px;
  }

  @media (max-width: 1080px) {
    font-size: 29px;
  }

  @media (max-width: 992px) {

  }

  @media (max-width: 767px) {
    font-size: 24px;
  }

  @media (max-width: 580px) {
    font-size: 18px;
  }

  @media (max-width: 480px) {
    margin-top: 10px;
  }
}
