.grain-machines__gallery {
  @media (max-width: 992px) {
    margin-top: 35px;
  }
}

.grain-machines__gallery h2 {
  @media (max-width: 992px) {
    text-align: center;
  }
}

.grain-machines__gallery > p {
  padding-top: 75px;
  padding-left: 32px;
  padding-right: 25.8vw;

  @media (max-width: 1600px) {
    padding-top: 60px;
    padding-left: 27px;
  }

  @media (max-width: 1280px) {
    padding-top: 50px;
    padding-left: 21px;
  }

  @media (max-width: 1080px) {
    padding-right: 0;
  }

  @media (max-width: 992px) {
    font-size: 21px;
  }

  @media (max-width: 767px) {
    padding-left: 0;
    padding-right: 0;
  }

  @media (max-width: 480px) {

  }
}

.grain-machines__list {
  padding-top: 37px;

  @media (max-width: 1600px) {
    padding-top: 29px;
  }

  @media (max-width: 1280px) {
    padding-top: 24px;
  }
}

.grain-machines__list .label-cofemashines::before {
  @media (max-width: 1080px) {
    width: 119px;
    height: 43px;
    font-size: 20px;
  }

  @media (max-width: 992px) {
    width: 145px;
    height: 50px;
    font-size: 23px;
  }

  @media (max-width: 767px) {
    width: 119px;
    height: 43px;
    font-size: 20px;
  }

  @media (max-width: 680px) {
    width: 145px;
    height: 50px;
    font-size: 23px;
  }

  @media (max-width: 580px) {
    width: 119px;
    height: 43px;
    font-size: 20px;
  }

  @media (max-width: 480px) {
    width: 95px;
    height: 34px;
    font-size: 16px;
  }
}
