.description {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  align-items: center;
  justify-content: space-between;
  padding-top: 57px;
  padding-left: 9.9vw;
  padding-right: 9.9vw;
  padding-bottom: 73px;

  @media (max-width: 1600px) {
    padding-top: 50px;
    padding-left: 10vw;
    padding-right: 10vw;
  }

  @media (max-width: 1280px) {
    padding-top: 45px;
    padding-bottom: 60px;
  }

  @media (max-width: 1080px) {
    grid-template-columns: 1fr;
    row-gap: 65px;
    padding-left: 13.7vw;
    padding-right: 13.7vw;
  }

  @media (max-width: 767px) {
    padding-left: 10vw;
    padding-right: 10vw;
  }

  @media (max-width: 580px) {
    padding-left: 12vw;
    padding-right: 12vw;
  }

  @media (max-width: 380px) {
    padding-left: 8vw;
    padding-right: 8vw;
  }
}

.description > section {

}

.description h1 {
  font-family: inherit;
  font-size: 100px;
  line-height: 100 / 100 * 1;
  font-weight: 700;
  color: var(--basic-main);

  @media (max-width: 1600px) {
    font-size: 83px;
  }

  @media (max-width: 1280px) {
    font-size: 67px;
  }

  @media (max-width: 1080px) {
    font-size: 100px;
  }

  @media (max-width: 992px) {
    font-size: 80px;
  }

  @media (max-width: 767px) {
    font-size: 67px;
  }

  @media (max-width: 580px) {
    font-size: 55px;
  }

  @media (max-width: 480px) {
    font-size: 40px;
    text-align: center;
  }

  @media (max-width: 380px) {
    font-size: 34px;
  }
}

.description p {
  font-family: inherit;
  font-size: 30px;
  line-height: 32 / 30 * 1;
  font-weight: 300;
  color: var(--basic-main);
  letter-spacing: -1.5px;

  @media (max-width: 1600px) {
    font-size: 27px;
  }

  @media (max-width: 1280px) {
    font-size: 21px;
  }

  @media (max-width: 1080px) {
    font-size: 34px;
  }

  @media (max-width: 992px) {
    font-size: 30px;
  }

  @media (max-width: 767px) {
    font-size: 25px;
  }

  @media (max-width: 580px) {
    font-size: 21px;
  }

  @media (max-width: 480px) {
    text-align: center;
  }
}

.description p:not(:last-child) {
  margin-bottom: 25px;

  @media (max-width: 1600px) {
    margin-bottom: 20px;
  }

  @media (max-width: 1280px) {
    margin-bottom: 18px;
  }

  @media (max-width: 992px) {

  }

  @media (max-width: 767px) {

  }

  @media (max-width: 480px) {

  }
}

.description__img {
  display: grid;
  justify-self: end;
  margin-left: 35px;

  @media (max-width: 1080px) {
    justify-self: center;
    margin-left: 0;
  }

  @media (max-width: 992px) {
    display: none;
  }
}

.description a {
  color: var(--basic-main);
}

.description .button {
  color: var(--basic-text-button);
  text-decoration: none;
}
