.label-goods {
  position: relative;
}

.label-goods::before {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: inherit;
  font-size: 21px;
  line-height: 16 / 21 * 1;
  letter-spacing: -0.8px;
  color: var(--basic-white);
  text-align: center;
  background-repeat: no-repeat;
  background-size: contain;
  z-index: 10;

  @media (max-width: 1600px) {
    font-size: 18px;
  }

  @media (max-width: 1280px) {
    font-size: 15px;
  }

  @media (max-width: 1080px) {
    font-size: 21px;
  }

  @media (max-width: 992px) {

  }

  @media (max-width: 767px) {

  }

  @media (max-width: 480px) {

  }
}

.label-goods--hit::before {
  content: "";
  width: 118px;
  height: 110px;
  background-image: url("../img/hit-2.svg");

  @media (max-width: 1600px) {
    width: 98px;
    height: 93px;
  }

  @media (max-width: 1280px) {
    width: 77px;
    height: 73px;
  }

  @media (max-width: 1080px) {
    width: 118px;
    height: 110px;
    padding-top: 1.4vw;
    padding-left: 1.04vw;
  }

  @media (max-width: 992px) {

  }

  @media (max-width: 767px) {

  }

  @media (max-width: 480px) {

  }
}

.label-goods--new::before {
  content: "новинка";
  width: 105px;
  height: 64px;
  background-color: var(--basic-brand);
  border-radius: 50%;
  padding-top: 0;
  padding-left: 0;


  @media (max-width: 1600px) {
    width: 87px;
    height: 53px;
  }

